import * as React from "react";
import { blueGrey } from "@mui/material/colors";
import { Typography } from "@mui/material";
import { formatDate, hasBeenUpdated } from "libs/dateHelper";

const AuthorBio = ({ name, date, lastUpdate }) => {
  return (
    <Typography variant="subtitle1" component="i" color={blueGrey[300]}>
      <div>
        <span>{formatDate(date)} </span>
        {hasBeenUpdated(date, lastUpdate) && (
          <span>, {formatDate(lastUpdate, true)} </span>
        )}
        par {name}
      </div>
    </Typography>
  );
};

export default AuthorBio;
