import * as React from "react";
import { Link } from "gatsby";
import AuthorBio from "components/AuthorBio";
import { Layout } from "components/Layout";
import { Breadcrumbs, Link as MuiLink, Typography } from "@mui/material";
import HomeIcon from "@mui/icons-material/Home";

const WpPost = ({ pageContext }) => {
  const {
    frontmatter: { date, lastUpdate, title, tags, categories },
    html,
    id,
  } = pageContext;
  return (
    <Layout pageTitle={title} hasNav={false} isSmallTitle>
      <div className={`post-${id}`}>
        <Breadcrumbs aria-label="breadcrumb">
          <MuiLink
            sx={{ display: "flex", alignItems: "center" }}
            variant="subtitle1"
            component={Link}
            to={`/`}
            underline="none"
          >
            <HomeIcon sx={{ mr: 0.5 }} fontSize="inherit" />
          </MuiLink>
          {categories.map((cat) => (
            <MuiLink
              variant="subtitle1"
              component={Link}
              to={`/?category=${cat}`}
              underline="none"
              key={id}
            >
              {cat}
            </MuiLink>
          ))}
          <Typography
            sx={{ display: "flex", alignItems: "center" }}
            variant="subtitle1"
            dangerouslySetInnerHTML={{ __html: title }}
          />
        </Breadcrumbs>

        <h1 className="posts" dangerouslySetInnerHTML={{ __html: title }} />
        <div dangerouslySetInnerHTML={{ __html: html }} />

        <AuthorBio name="Behrouze" date={date} lastUpdate={lastUpdate} />
      </div>
    </Layout>
  );
};

export default WpPost;
